body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
h1, h2, h3, h4, h5, h6 {
font-family: 'Roboto Mono', monospace !important;
}

p, div {
font-family: 'Roboto Mono', monospace !important;
}
code {
font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.border-bottom {
    border-bottom-color: rgba(0, 64, 255, 0.2) !important;
}
a {
    color: rgba(0, 64, 255, 0.7) !important;
}
@media (max-width: 768px) {
    h1 {
        font-size: 1.1em;
    }
    h4 {
        font-size: .75em;
    }
    small {
        font-size: .5em;
    }
}